import { Table } from 'react-bootstrap'
import { useSelector } from 'react-redux'

function Cart() {

    let a = useSelector((state) => { return state });
    let aData = a.data;

    return (
        <Table>
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                {
                    aData.map((a, i) => {
                        return (
                            <tr>

                                <th> {aData[i].id}</th>
                                <th>{aData[i].name}</th>
                                <th>{aData[i].count + ''}</th>
                            </tr>
                        )
                    })
                }
            </tbody>
        </Table >
    )
}
export default Cart