import './App.css';
import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Navbar, Container, Nav, Row, Col, Button } from 'react-bootstrap';
import gearData from './data.js';
import { Route, Routes, Link, useNavigate, Outlet } from 'react-router-dom'
import mouseData from './mouse.js';
import keyboardData from './keyboard.js';
import Detail from './detail.js';
import axios from 'axios';
import Cart from './Cart.js';
import { click } from '@testing-library/user-event/dist/click.js';

function App() {
  let [colorNum, setColorNum] = useState(0);
  let navigate = useNavigate();
  let [mainData, setMainData] = useState(gearData);
  let [clickCount, setClickCount] = useState(0);
  let [loading, setLoading] = useState(false);

  return (
    <div className="App">
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container>
          <Navbar.Brand onClick={() => { navigate('/shop'); setColorNum(0) }} style={{ cursor: 'pointer', color: colorNum === 0 ? 'red' : 'black', marginRight: '30px', fontWeight: 'bold', textDecoration: 'none' }}>Gaming Shop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Link to={"/mouse"} onClick={() => { setColorNum(1) }} style={{ color: colorNum === 1 ? 'red' : 'black', cursor: 'pointer', marginRight: '30px', fontWeight: 'bold', textDecoration: 'none' }} >Mouse</Link>
              <Link to={"/keyboard"} onClick={() => { setColorNum(2) }} style={{ color: colorNum === 2 ? 'red' : 'black', cursor: 'pointer', marginRight: '30px', fontWeight: 'bold', textDecoration: 'none' }}>Keyboard</Link>
              <Link to={"/event"} onClick={() => { setColorNum(3) }} style={{ color: colorNum === 3 ? 'red' : 'black', cursor: 'pointer', marginRight: '30px', fontWeight: 'bold', textDecoration: 'none' }}>Event</Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>

      <Routes>
        <Route path='/shop' element={<>
          <div className='main-bg' />
          <Row>
            {
              mainData.map((a, i) => {
                return (
                  <Prize mainData={mainData[i]} i={i} />
                )
              })
            }
          </Row>
          <Row> {loading === true ? <h2>...  </h2> : ""}</Row>
          <Button variant='dark' onClick={() => {
            setLoading(true);
            setClickCount(++clickCount);
            if (clickCount === 1) {
              axios.get('https://codingapple1.github.io/shop/data2.json')
                .then((more) => {
                  let copy = [...mainData, ...more.data];
                  copy.map((a, i) => {
                    if (i > 2) {
                      copy[i].price = "$89.96";
                    }
                  })
                  setMainData(copy);
                  setLoading(false);
                })
            }
            if (clickCount === 2) {
              axios.get("https://codingapple1.github.io/shop/data2.json",
                "https://codingapple1.github.io/shop/data3.json")
                .then((more) => {
                  let copy = [...mainData, ...more.data];
                  copy.map((a, i) => {
                    if (i > 5) {
                      copy[i].price = "$106.42";
                    }
                    setMainData(copy);
                    setLoading(false);
                  })
                })
            }
            if (clickCount > 2) {
              alert('   ')
              setLoading(false);
            }

          }

          } style={{ marginTop: '10px' }}></Button>
        </>
        } />
        <Route path='/mouse' element={
          <Container style={{ border: 'solid 3px grey' }}>
            <h1 style={{ paddingTop: '10px', fontWeight: 'bold' }}>GAMING MOUSE TOP 4</h1>
            <Row>
              {
                mouseData.map((a, i) => {
                  if (i <= 1) {
                    return (
                      < MouseDetail mouseData={mouseData[i]} i={i} />
                    )
                  }
                })
              }
            </Row>
            <Row>
              {
                mouseData.map((a, i) => {
                  if (i >= 2) {
                    return (
                      < MouseDetail mouseData={mouseData[i]} i={i} />
                    )
                  }
                })
              }
            </Row>
          </Container>
        }>
        </Route>
        <Route path='/keyboard' element={
          <Container style={{ border: 'solid 3px grey' }}>
            <h1 style={{ paddingTop: '10px', fontWeight: 'bold' }}>GAMING KEYBOARD TOP 4</h1>
            <Row>
              {
                keyboardData.map((a, i) => {
                  if (i <= 1) {
                    return (
                      < KeyboardDetail keyboardData={keyboardData[i]} i={i} />
                    )
                  }
                })
              }
            </Row>
            <Row>
              {
                mouseData.map((a, i) => {
                  if (i >= 2) {
                    return (
                      < KeyboardDetail keyboardData={keyboardData[i]} i={i} />
                    )
                  }
                })
              }
            </Row>
          </Container>
        }></Route>
        <Route path='/event' element={
          <Container>
            <Row>
              <Col>
                <Button variant="primary" size="lg" onClick={() => { navigate('/event/one') }}>
                  <p>EVENT.1</p>
                  <h4> </h4>
                </Button>{' '}
              </Col>
              <Col>
                <Button variant="primary" size="lg" onClick={() => { navigate('/event/two') }}>
                  <p>EVENT.2</p>
                  <h4> </h4>
                </Button>{' '}
              </Col>
            </Row>
            <Outlet></Outlet>
          </Container>
        }>
          <Route path='one' element={<div style={{
            display: 'flex', justifyContent: 'center', alignItems: 'center', height: '400px'
          }}><h2 style={{ lineHeight: '50px' }}>  </h2>
          </div>}></Route>
          <Route path='two' element={<div style={{
            display: 'flex', justifyContent: 'center', alignItems: 'center', height: '400px'
          }}><h2 style={{ lineHeight: '50px' }}> </h2></div>}></Route>
        </Route>
        <Route path='/detail/:id' element={
          <Detail mainData={mainData} />
        }>
        </Route>
        <Route path="/cart" element={<Cart />} />
        <Route path='/detail/' element={
          <div style={{ display: 'flex', width: '100%', height: '600px', justifyContent: 'center', alignItems: 'center' }}>
            <h1> 3  </h1>
          </div>
        }></Route>
        <Route path='*' element={<div style={{ display: 'flex', width: '100%', height: '600px', justifyContent: 'center', alignItems: 'center' }}><h1>URL  </h1></div>}></Route>
      </Routes>

    </div >
  );
}

function Prize(props) {
  return (
    <Col style={{ marginRight: '7%', marginLeft: '7%' }}>
      <Link to={`/detail/${props.i >= 0 && props.i <= 2 ? props.i : ""}`}>
        <img src={
          "https://sif51.github.io/shop" + '/gamingGear' + props.i + '.jpg'
        }
          style={{ cursor: props.i <= 2 ? 'pointer' : 'default', outline: 'solid 3px red', marginBottom: '10px', width: "250px", height: "125px" }} />
      </Link>
      <h4>{props.mainData.title}</h4>
      <h2>{props.mainData.price}</h2>
    </Col>
  )
};

let MouseDetail = (props) => {
  return (
    <Col >
      <img src={"https://sif51.github.io/shop" + '/mouse' + props.i + '.jpg'} onClick={() => {
        if (props.i === 0) {
          window.location.href = "https://www.logitechg.com/ko-kr/products/gaming-mice/pro-x-superlight.html";
        };
        if (props.i === 1) {
          window.location.href = "https://www.razer.com/kr-kr/gaming-mice/razer-viper-ultimate";
        };
        if (props.i === 2) {
          window.location.href = "https://www.corsair.com/ko-kr/Categories/Products/Gaming-Mice/Darkcore-Pro-Wireless-Gaming-Mouse";
        };
        if (props.i === 3) {
          window.location.href = "https://www.logitechg.com/ko-kr/products/gaming-mice/g903-hero.html";
        };
      }} style={{ cursor: 'pointer', width: '300px', height: '300px', marginBottom: '10px', border: 'solid 3px lightgrey' }} />
      <h4>{props.mouseData.name}</h4>
      <h2>{props.mouseData.price}</h2>
    </Col>
  )
};

let KeyboardDetail = (props) => {
  return (
    <Col >
      <img src={"https://sif51.github.io/shop" + '/keyboard' + props.i + '.jpg'}
        onClick={() => {
          if (props.i === 0) {
            window.location.href = "https://search.shopping.naver.com/catalog/33954739618?query=%EC%BB%A4%EC%84%B8%EC%96%B4%20K70%20RGB%20TKL%20%EC%B1%94%ED%94%BC%EC%96%B8&NaPm=ct%3Dlswwe3w8%7Cci%3D71aad3ade1d558536d3e320b0111184a03cce52a%7Ctr%3Dslsl%7Csn%3D95694%7Chk%3Dc5852dbc6f4f6fe264cbb6ceb63c54f1970a0a15";
          };
          if (props.i === 1) {
            window.location.href = "https://search.shopping.naver.com/catalog/20719446418?query=%EB%A1%9C%EC%A7%80%ED%85%8D%20G913%20TKL%20%EB%AC%B4%EC%84%A0&NaPm=ct%3Dlswwfac0%7Cci%3Db5bea69d8956ca23ca67ca23d3dd854f9a2d7e93%7Ctr%3Dslsl%7Csn%3D95694%7Chk%3Dcc57c1f8b7f392d2830cc6553fde664384ae50d9";
          };
          if (props.i === 2) {
            window.location.href = "https://search.shopping.naver.com/catalog/27227009523?query=%EB%A0%88%EC%9D%B4%EC%A0%80%20%EB%B8%94%EB%9E%99%EC%9C%84%EB%8F%84%EC%9A%B0%20V3%20TKL%20%EC%9C%A0%EC%84%A0%20%EA%B2%8C%EC%9D%B4%EB%B0%8D%20%ED%82%A4%EB%B3%B4%EB%93%9C&NaPm=ct%3Dlswwbr0o%7Cci%3D1f1a1468409fec78ec2b3a82e6bbc2f04eadf7bb%7Ctr%3Dslsl%7Csn%3D95694%7Chk%3D49e6ce41520aefb355d95f6d0cfb8a1802c2509a";
          };
          if (props.i === 3) {
            window.location.href = "https://search.shopping.naver.com/catalog/32198184620?query=ASUS%20ROG%20Strix%20Flare%20II&NaPm=ct%3Dlswwhceo%7Cci%3D23a2d6fddde211d93fb5bca0bc08fd13040c3a84%7Ctr%3Dslsl%7Csn%3D95694%7Chk%3D1caec852c88b687ca082540cb73585a4f9d6a8be";
          };
        }} style={{ cursor: 'pointer', width: '300px', height: '300px', marginBottom: '10px', border: 'solid 3px lightgrey' }} />
      <h4>{props.keyboardData.name}</h4>
      <h2>{props.keyboardData.price}</h2>
    </Col>
  )
};

export default App;
