import { Row, Col, Container, Button, Form, InputGroup } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
import tap from './detailTap';

function Detail(props) {

    let { id } = useParams();
    let idNum = props.mainData.find((x) => x.id == id);
    let [info, setInfo] = useState(true);
    let [num, setNum] = useState('');
    let [detailTapNum, setDetailTapNum] = useState(0);
    let detailTap = ["", "", "Q&A"];
    let [deAni, setDeAni] = useState('');

    useEffect(() => {
        setDeAni('end');
    }, []);

    useEffect(() => {
        let timer = setTimeout(() => {
            setInfo(false);
        }, 2000);

        return () => {
            clearTimeout(timer)
        }
    }, []);
    useEffect(() => {
        if (isNaN(num) == true) {
            alert(' ');
            document.querySelector('.numInput').value = null;
        }
    }, [num]);

    const changeNum = (e) => {
        setNum(e.target.value);
    };

    return (
        <Container className={'start ' + deAni}>
            <div>
                {
                    info == true ? <div style={{ display: 'flex', justifyContent: 'center', padding: '20px', backgroundColor: 'lightYellow' }}>2   </div> : ""
                }
            </div>
            <Row style={{ alignItems: 'center', justifyContent: 'center' }}>
                <Col>
                    <img src={"https://sif51.github.io/shop" + '/gamingGear' + id + '.jpg'}
                        style={{ outline: 'solid 3px red', marginBottom: '10px', width: "600px", height: "300px" }} />
                </Col>
                <Col style={{ marginTop: 'auto', marginBottom: '3%' }}>
                    <h4 style={{ fontSize: '50px' }}>{idNum.title}</h4>
                    <p style={{ fontSize: '20px' }}>{idNum.content}</p>
                    <h2 style={{ marginBottom: '30px', fontSize: '40px' }}>{idNum.price}</h2>
                    <InputGroup size="sm" className="mb-3"><Form.Control aria-label="Small" aria-describedby="inputGroup-sizing-sm" className='numInput' onInput={changeNum} placeholder=' ' style={{ backgroundColor: 'lightgrey' }} ></Form.Control></InputGroup>
                    <Button variant="dark" style={{ marginLeft: '10px' }} onClick={() => {
                        if (document.querySelector('.numInput').value == "") { alert(' ') } else { alert('  ') }
                    }}></Button>
                </Col>
            </Row>
            <Row>
                {detailTap.map((a, i) => {
                    return (
                        <Col style={{ border: 'solid 2px grey', cursor: 'pointer', marginTop: '40px' }}
                            onClick={() => {
                                setDetailTapNum(i);
                            }}>
                            <h2>{detailTap[i]}</h2>
                        </Col>
                    )
                })}
            </Row>
            <Row style={{ border: 'solid 2px black', paddingBottom: '10%', marginBottom: '3%' }}>
                <Tap detailTapNum={detailTapNum} />
            </Row>
        </Container>
    )
};

function Tap(props) {

    let ani = 'end';
    let [aniNum, setAniNum] = useState(null);

    useEffect(() => {
        setAniNum(props.detailTapNum)
    }, [props.detailTapNum])

    if (props.detailTapNum === 0) {
        return (
            <Col className={'start ' + (aniNum === 0 ? ani : '')} style={{ marginTop: '5%', textAlign: 'left', paddingLeft: '3%' }}>
                <h4>{tap[0].content}</h4>
            </Col>
        )
    } else if (props.detailTapNum === 1) {
        return (
            <Col className={'start ' + (aniNum === 1 ? ani : '')} style={{ marginTop: '5%', textAlign: 'left', paddingLeft: '3%' }}>
                <h4 style={{ paddingBottom: '1%' }}></h4>
                <p style={{ fontSize: '20px', fontWeight: 'bold' }}>{tap[1].user}</p>
                <h4>{tap[1].content}</h4>
                <h4 style={{ paddingTop: '1%' }}></h4>
                <p style={{ fontSize: '20px', fontWeight: 'bold', paddingTop: '2%' }}>{tap[1].user1}</p>
                <h4>{tap[1].content1}</h4>
            </Col>
        )
    } else if (props.detailTapNum === 2) {
        return (
            <Col className={'start ' + (aniNum === 2 ? ani : '')} style={{ marginTop: '5%', textAlign: 'left', paddingLeft: '3%' }}>
                <p style={{ fontSize: '20px', fontWeight: 'bold' }}>{tap[2].user}</p>
                <h4 style={{ paddingBottom: '1%' }}>{tap[2].question}</h4>
                <h4>{tap[2].answer}</h4>
            </Col>
        )
    }
}

export default Detail;